.container {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 30px 10px;
  max-width: 1440px;
  height: 100vh;
}

@media screen and (min-width: 375px) {
  .container {
    padding: 50px 30px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    padding: 50px 100px 100px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    padding: 50px 128px 100px;
  }
}

.section {
  display: block;
  align-items: center;
  gap: 20px;
  width: 100%;
  margin-bottom: 60px;
}

@media screen and (min-width: 768px) {
  .section {
    display: flex;
  }
}

@media screen and (min-width: 768px) {
  .sectionAboutUs {
    flex-direction: row-reverse;
  }
}

.carImage {
  width: 100%;
  filter: drop-shadow(0 2mm 1mm rgba(54, 54, 55, 0.5));
  z-index: -100;
}

@media screen and (min-width: 768px) {
  .carImage {
    width: 50%;
  }
}

.heroTitle {
  margin-bottom: 50px;

  font-weight: 800;
  font-size: 65px;
  line-height: calc(70 / 65);
  text-transform: uppercase;

  filter: drop-shadow(0 2mm 1mm rgba(54, 54, 55, 0.8));
}

.accent {
  color: #3470ff;
}

.sectionTitle {
  margin-bottom: 25px;

  font-weight: 700;
  font-size: 36px;
  line-height: calc(42 / 36);
  text-transform: uppercase;

  filter: drop-shadow(0 2mm 1mm rgba(54, 54, 55, 0.8));
}

.sectionText {
  max-width: 600px;
  margin-bottom: 40px;

  text-align: justify;
  line-height: calc(20 / 16);

  color: rgba(18, 20, 23, 0.5);
}

.copyright {
  margin-top: auto;
}
